body {
  background: linear-gradient(174deg, #ffffff 0%, #a9aef4 100%) !important;
  font-family: "Asap", Helvetica, Arial, Lucida, sans-serif;
}
.wrapper {
  background: linear-gradient(
      to top left,
      rgba(236, 232, 55, 1),
      rgba(255, 255, 255, 0),
      white
    ),
    linear-gradient(
      to top right,
      rgba(190, 217, 124, 1),
      rgba(255, 255, 255, 0),
      white
    );
  display: block;
  width: 100%;
  border-radius: 5%;
  padding: 20px;
  margin: 50px;
}
body,
input,
label,
p {
  font-family: "Asap", Helvetica, Arial, Lucida, sans-serif;
  color: #333333;
}
h1,
h3 {
  text-align: center;
  padding-top: 20px;
}

.form-container {
  background-color: linear-gradient(
      89.71deg,
      #b2a7e9 -107.68%,
      rgba(178, 167, 233, 0) 101.77%
    ),
    #e6eaf4;
  font-family: "Asap", Helvetica, Arial, Lucida, sans-serif;
  width: 50%;
  display: block;
  margin: 0 auto;
  max-width: auto;
  border: bisque;
  background-color: #eaf3d5;
  box-shadow: 0px 16px 40px rgb(191 189 204 / 50%);
  padding: 50px;
  border-radius: 5%;
  text-align: justify;
}
.form-kidney {
  font-family: "Asap", Helvetica, Arial, Lucida, sans-serif;
  display: block;
  background-color: #eaf3d5;
  width: 50%;
  box-shadow: 0px 16px 40px rgb(191 189 204 / 50%);
  padding: 50px;
  margin: 0 auto;
  border-radius: 5%;
  text-align: justify;
}
.view-container {
  display: block;
  padding: 2px;
  text-align: fit;
  margin: 20px auto;
}
p {
  margin: 0;
}
.sysbp-input,
.diabp-input,
.date-input,
.kidney {
  width: 50%;
  height: 40px;
  background: #ffffff;
  box-shadow: 0px 0px 8px rgb(95 112 146 / 25%);
  border-radius: 7px;
  border: none;
  display: block;
  padding-left: 15px;
  font-size: 16px;
}
.btn-calc {
  background-color: #715dd7;
  border-radius: 10px;
  max-width: 250px;
  padding: 15px 25px;
  margin: 0 auto;
  text-align: center;
  font-size: 18px;
  line-height: 6px;
  letter-spacing: 0.5px;
  font-weight: 700;
  transition: background-color 0.3s ease-in-out;
}
.display-inline {
  white-space: pre-line;
}
